body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.hidden {
  display: none;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  /*flex-direction: column;*/
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 1vmin);
  color: white;
  margin-bottom: 80px;
}

.logo_container {
  display: flex;
}

.logo {
  align-self: center;
  margin-right: 20px;
  width: 120px;
}

.App-title {
  display: block;
  width: 110px;
  border: 1px solid white;
  padding: 5px;
}

.header-info {
    padding: 0 40px 0 20px;
}

.App-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.table {
  background-color: #566770;
  height: 50vh;
  width: 70vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  color: #e9eaeb;
  border-radius: 190px;
  box-shadow: 0 0 1px 30px #282c34, inset 0 0 1px #282c34;
}

.table-pots {
  display: flex;
  margin: 20px;
  flex: 1 1;
}

.table-board {
  display: flex;
  flex: 3 1;
  width: 375px;
  align-items: flex-start;
}

.bet_range {
  width: 300px;
}

.button {
  border-radius: 50%;
  width: 24px;
  height: 24px;
  color: crimson;
  background-color: #282c34;
  box-shadow: 3px 3px 6px #282c34, 2px 2px #3e4f5b;
}

.seats {
  /*display: flex;*/
  position: absolute;
  width: inherit;
  height: inherit;
}

.seat {
  display: block;
  position: absolute;
  color: #282c34;
  font-size: 16px;
  font-weight: bold;
  background-color: rgba(255,140,0,0.8);
  width: 22%;
  height: 100px;
  border-radius: 30px;
}

.seat.acting {
  border: 1px solid limegreen;
  box-shadow: 0 0 10px 3px limegreen;
}

.seat-winner {
  border: 1px solid fuchsia;
  box-shadow: 0 0 10px 3px fuchsia;
}

.seat p {
  margin: 10px;
}

.seat-bet {
  color: #e9eaeb;
  font-weight: normal;
}

.seat-1-9,
.seat-1-5 {
  right: 15%;
  top: -15%;
}

.seat-2-9 {
  right: -17%;
  top: 10%;
}

.seat-3-9,
.seat-2-5 {
  right: -17%;
  bottom: 10%;
}

.seat-4-9 {
  right: 8%;
  bottom: -15%;
}

.seat-5-9,
.seat-3-5,
.seat-2-3{
  left: 40%;
  bottom: -15%;
}

.seat-6-9 {
  left: 8%;
  bottom: -15%;
}

.seat-7-9,
.seat-4-5{
  left: -17%;
  bottom: 10%;
}

.seat-8-9 {
  left: -17%;
  top: 10%;
}

.seat-9-9,
.seat-5-5{
  left: 15%;
  top: -15%;
}

.seat-1-6,
.seat-1-4{
  right: 20%;
  top: -15%;
}

.seat-2-6,
.seat-1-2,
.seat-1-3{
  top: 40%;
  right: -20%;
}

.seat-3-6,
.seat-2-4{
  right: 20%;
  bottom: -15%;
}

.seat-4-6,
.seat-3-4{
  left: 20%;
  bottom: -15%;
}

.seat-5-6,
.seat-2-2,
.seat-3-3{
  top: 40%;
  left: -20%;
}

.seat-6-6,
.seat-4-4{
  left: 20%;
  top: -15%;
}

.seat-avatar {
  width: 80px;
  position: absolute;
}

.seat-right .seat-avatar{
  left: 10px;
  top: 10px;
}

.seat-left .seat-avatar{
  right: 10px;
  top: 10px;
}

.seat-right .seat-hand{
  position: absolute;
  right: 13px;
  top: 25%;
}

.seat-left .seat-hand {
  position: absolute;
  left: 13px;
  top: 25%;
}
.seat-table_info {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.seat-4-9 .seat-table_info,
.seat-5-9 .seat-table_info,
.seat-3-5 .seat-table_info,
.seat-2-3 .seat-table_info,
.seat-6-9 .seat-table_info,
.seat-3-6 .seat-table_info,
.seat-2-4 .seat-table_info,
.seat-4-6 .seat-table_info,
.seat-3-4 .seat-table_info {
  top: -80%;
  left: 45%;
}

.seat-1-9 .seat-table_info,
.seat-1-5 .seat-table_info,
.seat-9-9 .seat-table_info,
.seat-5-5 .seat-table_info,
.seat-1-6 .seat-table_info,
.seat-1-4 .seat-table_info,
.seat-6-6 .seat-table_info,
.seat-4-4 .seat-table_info {
  bottom: -80%;
  left: 45%;
}

.seat-2-9 .seat-table_info,
.seat-3-9 .seat-table_info,
.seat-2-5 .seat-table_info,
.seat-2-6 .seat-table_info,
.seat-1-2 .seat-table_info,
.seat-1-3 .seat-table_info {
  left: -30%;
  top: 10%;
}

.seat-7-9 .seat-table_info,
.seat-4-5 .seat-table_info,
.seat-8-9 .seat-table_info,
.seat-5-6 .seat-table_info,
.seat-2-2 .seat-table_info,
.seat-3-3 .seat-table_info {
  right: -30%;
  top: 10%;
}

.control_panel {
  position: fixed;
  right: 0;
  bottom: 0;
  padding: 30px;
  border: 4px solid #282c34;
  border-radius: 10px;
}
